import { Container, Button, ButtonGroup, Row, Col } from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import classes from './ExistingOrders.module.css';
import { fetchOrders } from "../lib/api";
import { useState } from "react";
import Card from "../components/UI/Card";

const ExistingOrders = () => {

    const [orders, setOrders] = useState([]);

    const progessOrderHandler = () => {
        fetchOrders('progress').then(result => setOrders(result));
    }

    const packedOrderHandler = () => {
        fetchOrders('packed').then(result => setOrders(result));
    }

    const deliveryOrderHandler = () => {
        fetchOrders('delivery').then(result => setOrders(result));
    }

    return(
        <Container className="">
            <ButtonGroup className={classes.buttonGroup}>
                <Button size="lg" onClick={progessOrderHandler}>Orders in Progress</Button>
                <Button variant="warning" size="lg" onClick={packedOrderHandler}>Orders Packed</Button>
                <Button variant="success" size="lg" onClick={deliveryOrderHandler}>Orders in Delivery</Button>
            </ButtonGroup>
            {orders && orders.map((order) => 
                <Card key={order.id}>
                    <Row>
                        <Col>{order.dryFruit}</Col>
                        <Col>{order.weight} grams</Col>
                        <Col>{order.canteenName}</Col>
                        <Col>{order.orderQuantity} boxes</Col>
                    </Row>
                    <footer className={classes.footer_time}>
                        {order.newOrderTime}
                    </footer>
                </Card>
            )}
        </Container>
    )
};

export default ExistingOrders;