import './App.css';
import NewOrder from './pages/NewOrder';
import NavigationBar from './components/Nav/NavigationBar';
import { Fragment } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import Home from './pages/Home';
import CurrentOrders from './pages/CurrentOrders';
import PackedItems from './pages/PackedItems';

function App() {
  return (
    <Fragment >
      <NavigationBar />
      <main className="App-header">
        <Switch>
        <Route path="/" exact>
            <Redirect to='/home' />
          </Route>
          <Route path="/home">
            <Home />
          </Route>
          <Route path="/neworder" exact>
            <NewOrder />
          </Route>
          <Route path="/currentorders" exact>
            <CurrentOrders />
          </Route>
          <Route path="/packeditems" exact>
            <PackedItems />
          </Route>
        </Switch>
      </main>
    </Fragment>
  );
}

export default App;
