const FIREBASE_DOMAIN = 'https://inventory-eab75-default-rtdb.firebaseio.com';

export async function sendOrder (data) {
    const response = await fetch(`${FIREBASE_DOMAIN}/newOrders.json`,{
        method:"POST",
        headers:{
            "Content-Type": "application/json"
        },
        body: JSON.stringify({
            id: data.key,
            dryFruit: data.dryFruit,
            weight: data.weight,
            canteenName: data.canteenName,
            orderQuantity: data.orderQuantity,
            newOrderTime: data.newOrderTime
        })
    });

    if(!response){
        throw new Error('New order not added');
    }
    
    return (response.json());
    
};

export async function fetchOrders (table) {
    
    let response;

    if(table === 'new'){
        response = await fetch(`${FIREBASE_DOMAIN}/newOrders.json`);
    }else if(table === 'progress'){
        response = await fetch(`${FIREBASE_DOMAIN}/progressOrders.json`);
    }else if(table === 'packed'){
        response = await fetch(`${FIREBASE_DOMAIN}/packedOrders.json`);
    }else if(table === 'delivery'){
        response = await fetch(`${FIREBASE_DOMAIN}/deliveryOrders.json`);
    }    
    
    if(!response.ok){
        throw new Error('Something went wrong');
    }

    const responseData = await response.json();

    const loadedOrders = [];

    for(const key in responseData ){
        loadedOrders.push({
          id: key,
          dryFruit: responseData[key].dryFruit,
          weight: responseData[key].weight,
          canteenName: responseData[key].canteenName,
          orderQuantity: responseData[key].orderQuantity,
          newOrderTime: responseData[key].newOrderTime
        });
    }

    return loadedOrders;
};


export async function progressOrders (found){
    const loadedOrders = await fetchOrders('progress');    
    let result = loadedOrders.find(e => 
            (e.canteenName === found.canteenName && e.dryFruit === found.dryFruit &&
                e.orderQuantity === found.orderQuantity && e.weight === found.weight)
        );
    if(result){
        alert('Order already in progress state');      
        result = "";  
    }else{
        const response = await fetch(`${FIREBASE_DOMAIN}/progressOrders.json`,{
            method:"POST",
            headers:{
                "Content-Type": "application/json"
            },
            body: JSON.stringify({
                id: found.key,
                dryFruit: found.dryFruit,
                weight: found.weight,
                canteenName: found.canteenName,
                orderQuantity: found.orderQuantity,
                newOrderTime: found.newOrderTime
            })
        });
        if(!response){
            throw Error('Order not in progress');
        }else{
            alert('Order is in progress now.');
        };
    }
     
};

