import { useState } from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Container, Form, Button} from 'react-bootstrap';
import classes from './ItemForm.module.css';
import { sendOrder } from "../../lib/api";


const ItemForm = (props) => {

    const [enteredDryFruit, isEnteredDryFruit] = useState('');
    const [enteredCanteenName, isEnteredCanteenName] = useState('');
    const [enteredQuantity, isEnteredQuantity] = useState('');
    const [enteredWeight, isEnteredWeight] = useState('');
    const [time, setTime] = useState('');
    const id="";

    const newOrders = {
        id,
        dryFruit: enteredDryFruit,
        weight: enteredWeight,
        canteenName: enteredCanteenName,
        orderQuantity: enteredQuantity,
        newOrderTime: time
    }

    const itemChangeHandler = event => {
        isEnteredDryFruit(event.target.value);
    };

    const weightChangeHandler = event => {
        isEnteredWeight(event.target.value);
    };

    const canteenNameHandler = event => {
        isEnteredCanteenName(event.target.value);
    };

    const dryFruitQuantityHandler = event => {
        isEnteredQuantity(event.target.value);
        setTime(new Date().toLocaleString() + "");
    };

    const submitFormHandler = event => {
        event.preventDefault();
        if(enteredDryFruit !== '' && enteredCanteenName !== '' && enteredQuantity !== 0 && enteredWeight !== 0){
           checkTypeOfOrder();
        }
        isEnteredCanteenName('');
        isEnteredDryFruit('');
        isEnteredQuantity('');
        isEnteredWeight('');
    }

    const checkTypeOfOrder = () =>{
        const orderType = props.onAdd;
        if(orderType === 'newOrder'){
            sendOrder(newOrders);
        }else if(orderType === 'packedItems'){
            return;
        }
    }

    return(
        <Container>
            <Form onSubmit={submitFormHandler} >
                <Form.Label>Please enter item: </Form.Label>
                <Form.Select name='option' onChange={itemChangeHandler} className={classes.enteries} required>
                    <option></option>
                    <option value='Cashew Standard A320'>Cashew Standard A320</option>
                    <option value='Cashew Premium Tukda'>Cashew Premium Tukda</option>
                    <option value='Cashew Standard W320'>Cashew Standard W320</option>
                    <option value='Cashew Standard A240'>Cashew Standard A240</option>
                    <option value='Date'>Date</option>
                    <option value='Almond'>Almond</option>
                    <option value='Kismis'>Kismis</option>
                    <option value='Black Pepper'>Black Pepper</option>
                    <option value='Garam Masala Sabut'>Garam Masala Sabut</option>
                    <option value='Jeera Sabut'>Jeera Sabut</option>
                    <option value='Sarso'>Sarso</option>
                    <option value='Sounf'>Sounf</option>
                    <option value='Methi Dana'>Methi Dana</option>
                    <option value='Soya Chunks'>Soya Chunks</option>
                    <option value='Coriander'>Coriander</option>
                    <option value='Ajwain'>Ajwain</option>
                    <option value='Green Elaichi'>Green Elaichi</option>
                    <option value='Moti Elaichi'>Moti Elaichi</option>
                    <option value='Arhar Dal'>Arhar Dal</option>
                    <option value='Masoor Dal'>Masoor Dal</option>
                    <option value='Chana Dal'>Chana Dal</option>
                    <option value='Clove'>Clove</option>
                    <option value='Udid Dal'>Udid Dal</option>
                    <option value='Dried Fig'>Dried Fig</option>
                    <option value='Pistachio'>Pistachio</option>
                    <option value='Roasted and Salted Pistachio'>Roasted and Salted Pistachio</option>
                    <option value='Walnut'>Walnut</option>
                </Form.Select>
                <p>
                    <Form.Label>Please the enter the weight: </Form.Label>
                    <Form.Select name='option' onChange={weightChangeHandler} className={classes.enteries} required >
                        <option></option>
                        <option value='50'>50 grams</option>
                        <option value='100'>100 grams</option>
                        <option value='200'>200 grams</option>
                        <option value='250'>250 grams</option>
                        <option value='300'>300 grams</option>
                        <option value='500'>500 grams</option>
                        <option value='1000'>1 kilograms</option>
                    </Form.Select>
                </p>
                <p>
                    <Form.Label>Please the enter the canteen name: </Form.Label>
                    <Form.Control type="text" className={classes.enteries} onChange={canteenNameHandler} value={enteredCanteenName} required />
                </p>
                <p>
                    <Form.Label>Please the enter the number of boxes: </Form.Label>
                    <Form.Control type="text" className={classes.enteries} onChange={dryFruitQuantityHandler} value={enteredQuantity} required />
                </p>
                <Button type='submit'>Submit</Button>
            </Form>    
        </Container>
    )
}

export default ItemForm;