import { Container } from "react-bootstrap";
import nickMicLogo from '../assests/nickMicLogo.png';

const Home = () => {
    return(
        <Container>
            <h2 style={ {color: "white"} }>LeeChant Group</h2>
            <p className="bold">
                Leechants Global Group has immense three decades of marketing experience of Food products. 
                Today marketing conditions are better, while the competition is same. 
                We have faced hardship in marketing products and evolved in more and better ways. 
                We had vision of an importance of nutrition in everyone’s life. 
                As the core purpose to serve the good food we took steps in food market. 
                We are manufacturing, marketing nutrient-rich food products, creating position in every household. 
                Experiencing different conditions of market, understanding the need of nutrition, we are marketing food products with assurance of health and hygiene. 
                We assure Food products and nutrition is inseparable is our uncommon.
                Our main aim is to market natural and innovative quality health products. 
                We market wide range of FMCG products pan India in large institutions and niche sectors segments. 
                We are ISO 9001-2015 Company, Registered as MSME with “Aatm Nirbhar Bharat” concept as core value.
            </p>
            <img 
                src={nickMicLogo}
                alt="NickMic Logo"
            />
            
        </Container>
    )
};

export default Home;