import Card from "../components/UI/Card";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Row , Col, ButtonGroup} from 'react-bootstrap';
import classes from './CurrentOrders.module.css';
import { useState, useEffect } from "react";
import { fetchOrders, progressOrders, packedOrders, deliveryOrders } from "../lib/api";

const CurrentOrders = () => {

    const [orders, setOrders] = useState([]);
    
    useEffect(() => {
        fetchOrders('new')
            .then((result) => setOrders(result));
    },[]);

    const progessHandler = (evt) => {
        const found = orders.find(order => order.id === evt.target.value);
        progressOrders(found);
    };

    const packedHandler = (evt) => {
        const found = orders.find(order => order.id === evt.target.value);
        packedOrders(found);
    };

    const deliveryHandler = (evt) => {
        const found = orders.find(order => order.id === evt.target.value);
        deliveryOrders(found);
    }

    return( 
        <div className={classes.card}>
            {orders.map((order) => 
                <Card key={order.id}>
                    <Row >
                        <Col>
                            {order.dryFruit}
                        </Col>
                        <Col>
                            {order.weight} grams
                        </Col>
                        <Col>
                            {order.canteenName}
                        </Col>
                        <Col>
                            {order.orderQuantity} boxes
                        </Col>
                    </Row>
                    <Row>
                        <ButtonGroup>
                            <Button className={classes.btn}  onClick={progessHandler} value={order.id}>
                                Progress
                            </Button>
                            <Button variant="warning" className={classes.btn} onClick={packedHandler} value={order.id}>
                                Packed
                            </Button>
                            <Button variant="success" className={classes.btn} onClick={deliveryHandler} value={order.id}>
                                Delivery
                            </Button>
                        </ButtonGroup>
                    </Row>
                    <footer className={classes.footer_time}>
                        {order.newOrderTime}
                    </footer>
                </Card>
            )}
        </div>
    )
};

export default CurrentOrders;