import { NavLink } from 'react-router-dom';
import classes from './NavigationBar.module.css';

const NavigationBar = () => {
    return(
        <header className={classes.header}>
            <nav>
                <ul>
                    <li>
                        <NavLink activeClassName={classes.active} to="/home">Home</NavLink>
                    </li>
                    <li>
                        <NavLink activeClassName={classes.active} to="/neworder">New Order</NavLink>
                    </li>
                    <li>
                        <NavLink activeClassName={classes.active} to="/currentorders">Current Orders</NavLink>
                    </li>
                    <li>
                        <NavLink activeClassName={classes.active} to="/packeditems">Packed Items</NavLink>
                    </li>
                </ul>
            </nav>
        </header>
    )
};

export default NavigationBar;